import { defineDocumentType, makeSource } from "contentlayer/source-files"; //eslint-disable-line

const getLocale = (path: string) => {
  const pathArray = path.split(".");
  console.log(pathArray);
  return pathArray.length > 2 ? pathArray.slice(-2)[0] : "en";
};

export const Post = defineDocumentType(() => ({
  name: "Post",
  filePathPattern: "**/*.mdx",
  contentType: "mdx",
  fields: {
    title: {
      type: "string",
      required: true,
    },
    date: {
      type: "date",
      required: true,
    },
    summary: {
      type: "string",
      required: true,
    },
    tags: {
      type: "json",
      required: false,
    },
  },
  computedFields: {
    url: {
      type: "string",
      resolve: (post) => `/blog/${post._raw.flattenedPath.split(".")[0]}`,
    },
    locale: {
      type: "string",
      resolve: (post) => {
        console.log("DOC", post);
        return getLocale(post._raw.sourceFileDir);
      },
    },
  },
}));

export default makeSource({
  contentDirPath: "blog",
  documentTypes: [Post],
});
